/* Portfolio.css */
.portfolio {
  padding: 2rem; /* فضای داخلی */
}

.title {
  text-align: center; /* تراز عنوان به وسط */
  margin-bottom: 2rem; /* فاصله زیر عنوان */
  font-size: 2.5rem;
}

.category-header {
  justify-content: center; /* وسط‌چین کردن دسته‌بندی‌ها */
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 4rem;
  margin-bottom: 8rem;
  flex-wrap: wrap;
}

.portfolio-card {
  margin-bottom: 1.5rem; /* فاصله بین کارت‌ها */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* سایه برای کارت‌ها */
  transition: transform 0.2s; /* انیمیشن برای افکت hover */
}

.portfolio-card:hover {
  transform: scale(1.05); /* بزرگ شدن کارت هنگام hover */
}

.nav-item{
  padding: 1rem 2rem;

}

.nav-item--active{
  background-color: var(--primaryColor);
  border-radius: 0.3rem;
}

.nav-link{
    font-size: 1.5rem;
    color: black;
}

.portfolio-card{
    width: 100%;
    height: 100%;
    
}

.portfolio-img{
    width: inherit;
    height: inherit;
}

