.service-card-wrapper{
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
padding: 2.5rem 2rem;
border: 0.1rem solid #eee;
border-radius: 2rem;
}

.icon-wrapperr{
    background-color: var(--primaryColor);
    /* padding: 2rem; */
    width: 3rem;
    height: 3rem;

    border-radius: 0.3rem;
}

.service-icon{
    width: inherit;
    height: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
}

.services-title{
    font-size: 2rem;
    color: #151515;
    margin: 1rem 0;
}

.services-p{
    text-align: center;
    font-size: 1.3rem;
    margin-top: 0;
    font-weight: bold;
    color: #B3B3B3;
}