/* MainHeader.css */
.main-header {
  /* height: 80rem; */
   background: linear-gradient(rgba(59, 55, 53, 0.8), rgba(59, 55, 53, 0.5)),
    url("../../../public/image/bussein.jpg");
  background-size: cover;
  background-position: center;
  position: relative;

  margin-bottom: 8rem;


}


.main-header__content{
  display: flex;
  flex-direction: column;
  width: 100%;
  text-align: center;
  margin-top: 10rem;
  
  
}


.overlays {
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6); /* اپاسیتی تیره */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
}


.titlee {
  font-size: 6rem;
  margin: 0;
  color: white;
  font-family: sans-serif;
  font-weight: bold;
}

.title-kasuka {
  color: #ffc451;
  font-size: 4rem;
  font-family: sans-serif;
  margin: 1rem;
}

.main-des {
  font-size: 3.2rem !important;
  margin-top: 2rem;
  font-family: sans-serif !important;
  margin-bottom: 10rem !important;
  color: white;
}

